import React, { Component, PureComponent } from 'react';
import SlipMove from './SlipMove';
import './App.css';
import { move, shuffle, reverse } from './array';
import { transform } from 'async';

const generateItems = length => [...Array(length).keys()].map(k => ({
  id:`Item ${k}`,
  text:`Item ${k}`,
  opacity:1
}));

const actions = {
  reverse: ({items}) => ({items: reverse(items)}),
  shuffle: ({items}) => ({items: shuffle(items)}),
  move: (oldIndex, newIndex) => ({items}) => ({items: move(items, oldIndex, newIndex)}),
}

const stopEvent = e => {
  console.log('stoppingEvent',e.type, e.target);
  // e.preventDefault();
  // e.stopPropagation();
}

let next = 0;
export default class extends Component {

  state = { items: generateItems(20) }
  
  move = ({oldIndex, newIndex}) => this.setState(actions.move(oldIndex, newIndex));
  reverse = () => this.setState(actions.reverse);
  shuffle = () => this.setState(actions.shuffle);
  swap = (id) => {
    const items = this.state.items.slice();
    const item = items[next];
    items[next] = items[next+1];
    items[next+1] = item;
    this.setState({items});
    next++;
  }
  onDragOver = () => console.log("onDragOver");
  render() {
    const { items } = this.state;
    return (
      <div className="App"  droppable="true"  onDragOver={this.onDragOver} onDrop={this.onDragOver} >
        <button onClick={this.reverse}>Reverse</button>
        <button onClick={this.shuffle}>Shuffle</button>
        <Draggable/>
        <div className="list" >
          {/* <SlipMove onMoveEnd={this.move} flipMoveProps={{appearAnimation: 'fade'}} > */}
            {items.map((item, i) => {
              const placeholder = item.text === 'Item ' + next;
              return <ListItem id={item.id} placeholder={placeholder} swap={this.swap} key={item.id}>{item.text}</ListItem>
            })}
          {/* </SlipMove> */}
        </div>
      </div>
    );
  }
}


class Draggable extends Component {
  onTouchStart = e => {
    console.log("onTouchStart", e.touches, e.target.style);
    const {top, left} = e.target.getBoundingClientRect();
    this.position = {top, left};
    console.log(this.position) ;
    e.target.style.pointerEvents = 'none';
  }

  onTouchEnd = e => {
    e.target.style.pointerEvents = '';

  }

  onTouchMove = e => {
    // stopEvent(e);
    console.log("onTouchMove", e.target);
    // e.target.style.transform = `translate(${e.touches[0].clientX-this.position.left}px, ${e.touches[0].clientY-this.position.top}px)`;    
    e.target.style.transform = `translate(0px, ${e.touches[0].clientY-this.position.top}px)`;    
    e.target.style.zIndex = '999';
     
    console.log(document.elementFromPoint(e.touches[0].clientX, e.touches[0].clientY));
  }
  startPosition = null;
  position = {};

  render() {
    return <div 
        ref={this.setEl} 
        className="placeholder" 
        draggable="true" 
        onTouchMove={this.onTouchMove}  
        onTouchStart={this.onTouchStart}
        onTouchEnd={this.onTouchEnd}
      />
  }
}

// FlipMove requires class compoonents as children
class ListItem extends Component {
  onMouseLeave = (e) => {
    console.log('onMouseLeave', this.props.id);
    this.props.swap(this.props.id);
  }
  onDragOver=() => {
    console.log("onDragOver");
  }
  hide = () => {
    // this.setState({opacity:0});
    this.props.swap(this.props.id);
  }
  render() {
    const { children, placeholder } = this.props;
    const className = placeholder ? 'placeholder' : 'list-item'
    return <div className={className} droppable="true" onDragOver={this.onDragOver} onClick={this.hide} onPointerOut={this.onMouseLeave}>{children}</div>
  }
}
